import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { getStaticPage } from "../../api/staticPage";
import Loader from "../../components/features/loaders/loader";
import { useHistory } from "react-router-dom";
import market from "../../config/market";
import GenereteBody from "./GenereteBody";

function StaticPage({ menu }) {
	const [loading, setLoading] = useState(true);
	const [page, setPage] = useState("");
	const history = useHistory();

	if (!menu) {
		history.push(process.env.PUBLIC_URL + "/404");
	}

	useEffect(() => {
		setLoading(true);
		getStaticPage(menu.staticPage)
			.then((res) => {
				setPage(res.content);

				setLoading(false);
			})
			.catch((err) => {
				history.push(process.env.PUBLIC_URL + "/404");
			});
	}, [menu.id]);

	useEffect(() => {
		eval(page.split("<script>")[1]?.split("</script>")[0]);
	});

	if (loading) {
		return <Loader />;
	}

	return (
		<div className="container container-static-page mt-5">
			{market.config.home === 6 ? null : <h1>{menu.name}</h1>}
			<GenereteBody page={page} />
		</div>
	);
}

const mapStateToProps = (state, props) => {
	return {
		menu: state.menu.menuList.find(
			(menu) => menu.slug === props.match.params.slug
		),
		props: props,
	};
};

export default connect(mapStateToProps)(StaticPage);
